
/*///////////PROYECTOS////////////*/
section{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 20px;
}
.divSec{
    text-align: center;
}
.divSec h2{
    font-size: 32px;
}
#gridproy img{
    max-height: 315.75px;
}
#gridproy{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.indProy{ /*PROYECTO INDIVIDUAL*/
    text-align: center;
    margin: 20px 40px;
}
.indProy h3{
    font-size: 24px;
    margin: 5px;
}
.indProy p{
    font-size: 14px;
    margin: 5px auto 20px auto;
    width: 256px;
}
.indProy a{
    *background-color: #e51750;
    background-color: #504D84;
    font-size: 14px;
    color:#fffcfc;
    text-decoration: none;
    padding: 10px;
    border-radius: 8px;
}
.spikes {
    position: relative;
    background: #e9bad4;
    height: 50vh;
}

.spikes::after {
    content: '';
    position: absolute;
    right: 0;
    left: -22%;
    top: 100%;
    z-index: 10;
    display: block;
    height: 40px;
    background-size: 40px 100%;
    background-image: linear-gradient(135deg, #e9bad4 25%, transparent 25%), linear-gradient(225deg, #e9bad4 25%, transparent 25%);
    background-position: 0 0;
}